/*============ 1.navbar ============*/
.header-main {
  position: fixed;
  top: 0;
  height: 100%;
  z-index: 300;
  width: 200px;
  background-color: #17181b;
  overflow: auto;
  left: 0;
  overflow-x: hidden;
  text-align: center;
  z-index: 999;
  border-right: 1px solid #202226;
}
.header-main .image-container {
  position: relative;
}
.header-main .image-container img {
  position: relative;
}
.header-main .image-container .header-name {
  position: absolute;
  left: 0;
  bottom: 0;
  margin: 0;
  width: 100%;
  color: #fff;
  font-size: 1em;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 700;
  text-transform: uppercase;
  text-shadow: 0 2px 10px rgba(0, 0, 0, 0.5);
  z-index: 1;
  padding: 8px 0 8px 0;
}
.header-main .image-container .header-name:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #17181b;
  opacity: 0.8;
  z-index: -1;
}

.header-main nav.nav-menu ul li {
  font-size: 12px;
  letter-spacing: 1px;
  text-transform: uppercase;
  border-bottom: 1px solid #202226;
  text-align: left;
}
.header-main nav.nav-menu ul li a {
  display: block;
  line-height: 45px;
  padding-left: 70px;
  position: relative;
  color: #777;
}
.header-main nav.nav-menu ul li a span.nav-menu-icon {
  position: absolute;
  left: 40px;
  display: inline-block;
  font-size: 20px;
}
.header-main nav.nav-menu ul li a.active {
  color: #fff;
}

header .header-mobile {
  position: fixed;
  top: 0;
  left: 0;
  height: 50px;
  width: 100%;
  background-color: #17181b;
  z-index: 10;
  display: none;
  transition: all 0.3s ease-in-out;
}
header .header-mobile h2 {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  display: inline-block;
  color: #fff;
  font-size: 24px;
  text-transform: uppercase;
  line-height: 50px;
  white-space: nowrap;
}
header .header-mobile .header-toggle {
  font-size: 20px;
  line-height: 50px;
  cursor: pointer;
  width: 50px;
  text-align: center;
  border-left: 1px solid #3b3c43;
  float: right;
}
header .header-mobile .header-toggle i {
  color: #fff;
}

.header-main .nav-footer {
  margin-top: 2em;
  width: 100%;
}
.header-main .nav-footer .social {
  text-align: center;
  margin-bottom: 10px;
}
.header-main .nav-footer .social li {
  display: inline-block;
  font-size: 18px;
  margin-right: 10px;
}
.header-main .nav-footer .social li a {
  color: #777;
  transition: color 0.2s ease;
}
.header-main .nav-footer .social li:hover a {
  color: #fff;
}
.header-main .nav-footer .social li:last-child {
  margin-right: 0;
}
.header-main .nav-footer .copy p {
  font-size: 12px;
  color: #777;
  cursor: text;
}
/*============ 2.Main ============*/
.main {
  position: absolute;
    top: 0;
    left: 200px;
    right: 0;
    width: auto;
    height: 100vh;
    perspective: 1200px;
    overflow: hidden;
    transition: all 0.3s ease-in-out;
}
#home,
#about,
#experience,
#portfolio,
#skills,
#tools,
#contact {
  width: 100%;
  height: 100vh;
  transform-style: preserve-3d;
  transition: 1s transform;
  overflow-y: scroll;
}

/*============ 3.Home ============*/
.home-controller {
  display: none;
}
canvas {
  background: linear-gradient(
    to bottom,
    rgb(6,9,43) 0%,
    rgb(30,45,75) 100%
  );
}
.home-canvas-inner {
  background: linear-gradient(
    to bottom,
    rgb(6,9,43) 0%,
    rgb(30,45,75) 100%
  );
  display: flex;
  justify-content: center;
  align-items: center;
}
/*============ 4.about ============*/
.about-inner {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
	background: #8bf78b;
}
#desktop {
	width: 100%;
	height: 90vh;
}
.desktop-panel {
	width: 60%;
	height: 80px;
	background: #30312f;
	border-radius: 5px;
  align-self: center;
  display: grid;
}
.terminal {
	width: 45em;
  min-height: 350px;
  background: #300a24; 
  border-radius: 5px;
  margin: auto;
  position: absolute;
  right: 2%;
  top: 7%;
}
.term_top {
	width: 100%;
  display: grid;
  padding: 4px 0 4px 0;
  grid-template-columns: 2% 10% 20% 68%;
  align-items: center;
